import { Store } from "../helpers/ten-store";
import { Messengers } from "../helpers/Messengers";

export const getAdParameters = () => {
    const baseParameters = {
        ge: "",
        gr: "",
        ppid: "",
        postcode: "",
        timeStamp: Math.floor(Date.now() / 1000),
        region: Store.region?.state || "nsw",
        deviceId: Store.deviceId,
        is_lat: "0",
        idtype: "dpid",
    };

    if (!Store.UserInfo) return baseParameters;

    const { ge, gr, n10adId, postcode, memberId } = Store.UserInfo;
    const ppid = n10adId || memberId || "";

    return {
        ...baseParameters,
        ge,
        gr,
        ppid,
        postcode,
    };
};

const getFreewheelBVODParams = () => {
    const bvodResponseParams = Messengers.setBVODParams$.getCurrentValue();

    if (!bvodResponseParams?.length) {
      return '';
    }

    return bvodResponseParams.reduce((acc, curr, idx) => {
      const { BvodId, BvodIdSalt, Demo } = curr || {};

      const count = idx + 1;
      const bvodIdSaltValue =
        BvodIdSalt?.toLowerCase() === 'none' || !BvodIdSalt ? '' : BvodIdSalt;

      const oztamKeyValue = {
        [`imafw_oztam_${count}`]: BvodId,
        [`imafw_oztam_${count}salted`]: bvodIdSaltValue,
        [`imafw_oztam_${count}demo`]: Demo,
      };

      let oztamString = '';

      Object.keys(oztamKeyValue).forEach((key) => {
        const value = oztamKeyValue[key];
        if (value !== 'none') {
          oztamString += `&${key}=${encodeURIComponent(value)}`;
        }
      });

      return `${acc}${oztamString}`;
    }, '');
  };

  export const buildFreewheelAdUrl = async (
    urlFrame,
    genre,
    ozTamSessionId,
    authToken,
    assetId
  ) => {
    let adUrl = urlFrame;
    const tfcd = genre?.toLowerCase() === 'kids' ? 1 : 0;
    const { ge, gr, postcode, deviceId, is_lat } = getAdParameters();

    const region = Store.region.state || 'NSW';

    adUrl = adUrl.replace('\r\n ', '');
    adUrl = adUrl.replace(';', '&');
    adUrl = adUrl.replace(':', `${encodeURIComponent(':')}`);

    adUrl = adUrl.replace(/\[TFCD\]/g, String(tfcd));
    adUrl = adUrl.replace('%%ADVERTISING_IDENTIFIER_PLAIN%%', deviceId);
    adUrl = adUrl.replace('%%ADVERTISING_IDENTIFIER_IS_LAT%%', is_lat);
    adUrl += `&imafw__fw_h_x_country=AU&imafw__fw_content_id=${assetId}`;
    adUrl += '&imafw_mode=LIVE&imafw_vdty=VARIABLE';

    if (postcode) {
      adUrl += `&imafw__fw_h_x_postal_code=${String(postcode)}`;
    }

    const { fms_params } = Store.fmsResponse;

    adUrl = adUrl.replace(/\[GE\]/g, ge !== null ? String(ge) : '');
    adUrl = adUrl.replace(/\[GR\]/g, gr !== null ? String(gr) : '');
    adUrl = adUrl.replace(/\[PCODE\]/g, postcode !== null ? String(postcode) : '');
    adUrl = adUrl.replace(/\[STATE\]/g, region !== null ? String(region) : '');
    adUrl = adUrl.replace(/\[OZTAM_SESSION_ID\]/g, ozTamSessionId !== null ? String(ozTamSessionId) : '');
    adUrl = adUrl.replace(/\[SUB\]/g, fms_params ? String(fms_params.imafw_fms_userid) : '');

    const custParams = {
      imafw_tfcd: tfcd,
      ...fms_params,
      imafw_genre: genre,
    };

    const fwBvodParams = getFreewheelBVODParams();

    adUrl += fwBvodParams;

    Object.keys(custParams).forEach((key) => {
      const value = String(custParams[key]);
      adUrl += `&${key}=${encodeURIComponent(value)}`;
    });

    if (authToken) {
      adUrl += `&auth-token=${encodeURIComponent(authToken)}`;
    }

    return adUrl;
  };


export const buildGoogleAdUrl = (
    urlFrame,
    videoAltId,
    genre,
    ozTamSessionId
) => {
    let adUrl = urlFrame;
    const tfcd = genre?.toLowerCase() === "kids" ? 1 : 0;
    const {
        ge,
        gr,
        ppid,
        timeStamp,
        region,
        postcode,
        deviceId,
        is_lat,
        idtype,
    } = getAdParameters();

    adUrl = adUrl.replace(/\[TFCD\]/g, String(tfcd));
    adUrl = adUrl.replace(/\[PPID\]/g, ppid ?? "");
    adUrl = adUrl.replace(/\[GE\]/g, ge !== null ? String(ge) : "");
    adUrl = adUrl.replace(/\[GR\]/g, gr !== null ? String(gr) : "");
    adUrl = adUrl.replace(
        "&vid=[VID]",
        videoAltId != null ? `&vid=${videoAltId}` : ""
    );
    adUrl = adUrl.replace("&c=[TIMESTAMP]", `&c=${timeStamp}`);
    adUrl = adUrl.replace("%%ADVERTISING_IDENTIFIER_PLAIN%%", deviceId); // &rdid=
    adUrl = adUrl.replace("%%ADVERTISING_IDENTIFIER_IS_LAT%%", is_lat); // &is_lat=
    adUrl = adUrl.replace("%%ADVERTISING_IDENTIFIER_TYPE%%", idtype); // &idtype=

    if (ozTamSessionId) {
        if (adUrl.match(/\[OZTAM_SESSION_ID\]/g)) {
            adUrl = adUrl.replace(/\[OZTAM_SESSION_ID\]/g, ozTamSessionId);
        } else {
            adUrl += `%26session%3D${ozTamSessionId}`;
        }
    }

    if (
        ["nsw", "wa", "act", "nt", "sa", "tas", "vic", "qld"].includes(
            region.toLowerCase()
        )
    ) {
        adUrl += `%26state%3D${region.toLowerCase()}`;
    }

    if (postcode && /^\d{4}$/.exec(postcode)) {
        adUrl += `%26pcode%3D${postcode}`;
    }

    return adUrl;
};
