import { sendTenApiRequest } from './ten-api';
import { setTokenInDeviceStorage } from '../../provider-utils';

import { getAdParameters } from './ten-ad-parameters';
import { Store } from '../helpers/ten-store';

const REQUEST_TIMEOUT_MS = 10000;

const getUser = () => {
  const userInfo = Store || {};
  return {
    memberEmailHash: userInfo.auth.memberEmailHash || '',
    n10adId: userInfo.auth.n10adId || '',
    memberId: userInfo.auth.memberId || '',
    userId: userInfo.auth.UserId || '',
  };
};

const fmsRequestParameters = async () => {

    const adParams = getAdParameters()
    const ipResponse = Store.ipAddress

  const platform = Store.devicePlatform;
  const { idtype, is_lat } = adParams;
  const manufacturer = Store.ApiConfig.manufacturer;
  const { memberEmailHash, n10adId, memberId } = getUser();

  return {
    app: {
      bundle: `Tenplay_${manufacturer}-${platform}`,
      region: 'AU',
      googledai: 1,
      coppa: 0,
    },
    device: {
      lat: Number(is_lat),
      ip: ipResponse.ip,
      ua: navigator.userAgent,
    },
    identifiers: {
      emailhash: memberEmailHash,
      subscriberid: memberId,
      userid: n10adId,
      ifa: null,
      ifatype: idtype,
    },
  };
};

export const getCachedFMSResponse = () => {
  const cachedFMSData = Store.fmsResponse;
  // Check if any cached data exists
  if (!cachedFMSData) return null;

// Check the most recent FMS data
  const cachedFMSTimestamp = Number(Store.fmsResponseTimestamp);
  const fmsIdTTLMs = cachedFMSData.fms_id_ttl * 1000;

  if (Date.now() - cachedFMSTimestamp < fmsIdTTLMs) {
    return cachedFMSData;
  }
  return null;
};

const cacheFMSResponse = (data) => {
  Store.fmsResponse = data;
  Store.fmsResponseTimestamp = Date.now().toString();

  setTokenInDeviceStorage(
    "au.com.ten",
    btoa(
        JSON.stringify({
            refreshToken: Store.auth.refreshToken,
            alternativeToken: Store.auth.alternativeToken,
            fmsResponse: Store.fmsResponse,
            fmsResponseTimestamp: Store.fmsResponseTimestamp
        })
    )
);
};

export const fetchFMSResponse = async () => {
  const cachedFMSResponse = getCachedFMSResponse();
  if (cachedFMSResponse) {
    console.log(" cached FMS data available, not making a new call")
    return cachedFMSResponse;
  }

  const fmsRequestUrl = Store.ApiConfig.fmsRequestUrl;
  const { memberEmailHash, n10adId, userId } = getUser();
  const fallbackIds = memberEmailHash || n10adId || userId;
  const params = await fmsRequestParameters();
  const controller = new AbortController();
  const timeoutId = setTimeout(() => controller.abort(), REQUEST_TIMEOUT_MS);

    try {
      const response = await fetch(fmsRequestUrl, {
        method: 'POST',
        body: JSON.stringify(params),
        signal: controller.signal,
      });

      clearTimeout(timeoutId);

      if (response.ok) {
        const data = await response.json();
        cacheFMSResponse(data);
        return data;
      } else {
        throw new Error('Invalid response.');
      }
    } catch (err) {
      if (err.name === 'AbortError') {
        console.error('Request was aborted due to timeout.');
      }
      console.error(err);
      return {imafw__fw_vcid2: fallbackIds}
    }
};
