import Timezone from "../lib/timezone/index";
import { ACT, NSW, West, North, South, Queensland, Victoria, Tasmania} from '../lib/timezone/timezones'

import { Store } from "./ten-store";

const getRegionZoneObj = () => {
    const { state: region } = Store.region;
    const timezoneMap = {
        nsw: NSW,
        wa: West,
        act: ACT,
        nt: North,
        sa: South,
        tas: Tasmania,
        vic: Victoria,
        qld: Queensland,
    };
    return timezoneMap[region.toLowerCase()] || timezoneMap.nsw;
};

export const getTealiumLocalTimestamp = (unix) => {
    const timezone = getRegionZoneObj();
    const timezoneKey = Object.keys(timezone.zones)[0];
    const dateObj = Timezone(timezone);
    return dateObj(unix, timezoneKey, "%Y-%m-%dT%H:%M:%S");
};

export const getTealiumUTCTimestamp = (unix) => {
    const dateObj = Timezone("UTC");
    return dateObj(unix, "", "%Y-%m-%dT%H:%M:%S");
};
