import { OzTamClient } from "./OztamClient";
import { Store } from "../helpers/ten-store";
import { PlayerStates, generateUUID } from "../helpers/utils";
import { Messengers } from "../helpers/Messengers";
import { sendTenApiRequest } from "../ten-services/ten-api";

const AppVersion = Store.appVersion;

const PROGRESS_INTERVAL_SECONDS = 60;
const SESSION_UPDATER_TIMER_SECONDS = 10 * 60;

class OztamBridge {
    client = null;
    currentTime = -1;
    sessionStarted = false;
    currentAd = null;
    playerState = PlayerStates.Stopped;

    sessionStartedInAdBreak = false;
    progressInterval = 0;
    isAppHiddenFromView = false;

    // session 10 minute pause
    sessionRefreshHandler = null;
    justRegeneratedSessionId = false;

    // when tuning to a channel with parental PIN we will
    // get hidden true then false after the LOAD event has
    // been fired. In that case do not resend the LOAD event
    // unless the session updater has run
    hasStartedTrackingProgressDuringAppLaunch = false;

    listeners = [];

    init = () => {
        this.removeEventListeners(); // in case monitoringTeardown isn't called
        this.addListeners();
    };

    addListeners = () => {
        this.listeners = [
            ...this.listeners,
            Messengers.playerState$.addReceiver(this.onPlayerStateChange, true),
            Messengers.streamReady$.addReceiver(
                this.onChannelDataReceived,
                true
            ),
            Messengers.timeUpdate$.addReceiver(this.onTimeUpdate),
            Messengers.currentAd$.addReceiver(this.onAdChange),
            Messengers.playerError$.addReceiver(this.onAppErrored),
            Messengers.id3Metadata$.addReceiver(this.onFragTagParsed),
            Messengers.appTeardown$.addReceiver(this.onAppTeardown),
            Messengers.appHiddenWithoutAudio$.addReceiver(this.onAppHidden),
            Messengers.oztamSessionId$.addReceiver(this.onSessionIdChange),
        ];
    };

    removeEventListeners = () => {
        this.listeners.forEach((receiver) => receiver.unlisten());
        this.listeners = [];
    };

    onAppTeardown = () => {
        if (!this.isAppHiddenFromView && !this.adBreakOngoing) {
            this.client?.progressEvent(this.currentTime);
        }
        this.client?.setLastPosition(0);
        this.clearProgressInterval();
        this.clearSessionUpdater();
        // reset session ID after but before listener is
        // removed that sets on client
        Messengers.oztamSessionId$.send(generateUUID());
        this.removeEventListeners();
        this.sessionStarted = false;
        this.adBreakOngoing = false;
        this.isAppHiddenFromView = false;
        this.justRegeneratedSessionId = false;
        this.sessionStartedInAdBreak = false;
        this.currentTime = -1;
        this.currentAd = null;
        this.hasStartedTrackingProgressDuringAppLaunch = false;
        this.playerState = PlayerStates.Initialising;
    };

    onChannelDataReceived = async (channelData) => {
        if (!channelData?.liveShow) return;

        const ipAddress = Store.ipAddress.ip;

        const {
            liveShow: {
                publisherId,
                title: programName,
                id: seriesId,
                title: seriesName,
                genre,
            },
            channel: { id: channelId },
        } = channelData;

        const platformString =
            Store.deviceBrand.toLowerCase() === "soip-pck" ? "Puck" : "TV";

        const oztamStaticConfig = {
            vendorVersion: `Tenplay_Foxtel-${platformString}-Linear_${AppVersion}`,
            connectionType: Store.deviceNetworkConnection,
            deviceId: Store.deviceId,
            baseUrl: Store.ApiConfig.ozTamUrl,
            ipAddress: ipAddress,
            userId: Store.UserInfo.OzTamId,
        };

        const oztamStreamConfig = {
            eventIdentificationData: {
                publisherId,
                mediaId: channelData.liveStreamId,
                mediaDuration: 0,
                mediaType: "live",
            },
            streamMetadata: {
                channel: channelId,
                seriesId,
                seriesName,
                programName,
                genre,
            },
        };

        if (!this.client) {
            this.client = new OzTamClient(
                Messengers.oztamSessionId$.getCurrentValue(),
                Messengers
            );
        }
        this.client.init(oztamStaticConfig, oztamStreamConfig);
        this.client.loadEvent();
    };

    onTimeUpdate = (time) => {
        this.currentTime = time;
        if (!this.sessionStarted) {
            this.tryToBegin();
        }
    };

    onPlayerStateChange = (playerState) => {
        const previousState = this.playerState;
        this.playerState = playerState;

        if (this.isAppHiddenFromView) {
            return;
        }

        if (
            playerState === PlayerStates.Playing &&
            previousState !== PlayerStates.Playing
        ) {
            if (!this.currentAd && this.sessionStarted) {
                // Resume playing content after pause, seek etc
                console.log("[acc] oztam resumed");
                this.startProgressInterval();
                this.client?.setLastPosition(this.currentTime);
            }

            if (this.justRegeneratedSessionId) {
                // first playing event after regenerate -> send load & begin
                this.justRegeneratedSessionId = false;
                this.loadEvent(this.currentTime);
                this.tryToBegin();
            } else if (!this.sessionStarted) {
                // if start in ad break let ad begin event go first
                this.tryToBegin();
            }
        }

        // Log progress from last interval to when stream was paused
        // or seeked, buffering
        if (
            playerState !== PlayerStates.Playing &&
            previousState === PlayerStates.Playing &&
            !this.currentAd &&
            this.sessionStarted
        ) {
            console.log("[acc] oztam no longer playing");
            this.client?.progressEvent(this.currentTime);
            this.clearProgressInterval();
            this.setupSessionUpdater();
        }
    };

    onAdChange = (currentAd) => {
        const previousAd = this.currentAd;
        this.currentAd = currentAd;

        if (this.isAppHiddenFromView) return;

        // AD CHANGE IN BREAK
        if (previousAd && this.currentAd) {
            this.client?.adCompleteEvent(this.currentTime);
            this.client?.progressEvent(this.currentTime);
            this.client?.adBeginEvent(this.currentTime);

            // AD BREAK END
        } else if (previousAd && !this.currentAd) {
            this.client?.adCompleteEvent(this.currentTime);
            this.client?.progressEvent(this.currentTime);
            this.startProgressInterval();

            // AD BREAK START
        } else if (!previousAd && this.currentAd) {
            if (!this.sessionStarted) {
                this.tryToBegin();
            } else {
                this.client?.progressEvent(this.currentTime);
                this.client?.adBeginEvent(this.currentTime);
                this.clearProgressInterval();
            }
        }
    };

    tryToBegin = (restartExistingSession = false) => {
        if (
            this.isAppHiddenFromView ||
            this.playerState !== PlayerStates.Playing ||
            this.currentTime <= 0
        ) {
            return;
        }

        if (!this.sessionStarted || restartExistingSession) {
            if (!restartExistingSession) {
                this.client?.beginEvent(this.currentTime);
            }
            if (!!this.currentAd) {
                this.client.adBeginEvent(this.currentTime);
            } else {
                this.startProgressInterval();
            }
            this.sessionStarted = true;
        }
    };

    onFragTagParsed = (aampId3Data) => {
        const { presentationTime, timeScale, data } = aampId3Data.detail;
        const id3TagTime = presentationTime / timeScale;

        let rawTag = "";
        data.forEach((charCode) => {
            const char = String.fromCharCode(charCode);
            rawTag += char;
        });

        if (rawTag) {
            const isAdTag = rawTag.includes("google");
            if (isAdTag) {
                if (!this.sessionStarted) {
                    this.sessionStartedInAdBreak = true;
                }
            } else if (!this.sessionStartedInAdBreak) {
                // only set a pts epoch diff if we start in content
                // otherwise we won't know the correct starting value
                // and will jump back in time when content resumes
                const isoDateString = rawTag.replace(
                    /[^a-zA-Z0-9:\-.]|(ID3)|(TDRL)/g,
                    ""
                );
                const tagUnixTime =
                    new Date(isoDateString.toUpperCase()).getTime() / 1000;

                if (tagUnixTime) {
                    this.client?.setPtsEpochDiff(tagUnixTime - id3TagTime);
                    this.client?.setStopRecordingEvents(false);
                } else {
                    // from what we remember, ten wanted to not track at all if we dont have timing sorted
                    this.client?.setStopRecordingEvents(true);
                }
            }
        }
    };

    onAppHidden = (isHidden) => {
        if (this.isAppHiddenFromView === isHidden) return;
        this.isAppHiddenFromView = isHidden;
        console.log("[acc] change hidden state in oztam", isHidden);

        // change to hidden
        if (this.isAppHiddenFromView) {
            if (this.sessionStarted && !this.adBreakOngoing) {
                // partial progress
                this.client?.progressEvent(this.currentTime);
            }
            this.clearProgressInterval();
            if (
                this.sessionStarted ||
                !this.hasStartedTrackingProgressDuringAppLaunch
            ) {
                this.setupSessionUpdater();
            }
        } else {
            // change to foreground
            if (!this.sessionStarted) {
                // probably was hidden long enough to regenerate session ID
                if (this.hasStartedTrackingProgressDuringAppLaunch) {
                    // Only send load event if we've already tracked and are re-starting
                    // a sessionn
                    // Handles case where load event fired on startup and then
                    // again when hidden set to false
                    this.client?.loadEvent(this.currentTime);
                }
                // begin as new session
                this.tryToBegin();
            } else {
                // un-hidden within same session, try to begin means starting
                // again in ad break or content, pass true to bypass begin event
                this.client?.setLastPosition(this.currentTime);
                this.tryToBegin(true);
            }
        }
    };

    clearSessionUpdater = () => {
        if (this.sessionRefreshHandler) {
            console.log("[acc] oztam clear updater...");
            clearTimeout(this.sessionRefreshHandler);
            this.sessionRefreshHandler = null;
        }
    };
    setupSessionUpdater = () => {
        this.clearSessionUpdater();
        console.log("[acc] oztam setting up updater...");
        this.sessionRefreshHandler = window.setTimeout(() => {
            // should be cleared if we are unpaused or setting pro
            console.log("[acc] oztam updater updating Session ID!!");
            Messengers.oztamSessionId$.send(generateUUID());
            this.justRegeneratedSessionId = true;
            this.sessionStarted = false;

            // set this flag so we send out another load event
            // with updated session id
            // in case we are hidden and transition to foreground
            this.hasStartedTrackingProgressDuringAppLaunch = true;
        }, SESSION_UPDATER_TIMER_SECONDS * 1000);
    };

    onSessionIdChange = (sessionId) => {
        this.client?.setSessionID(sessionId);
    };

    startProgressInterval = () => {
        this.clearProgressInterval();
        this.clearSessionUpdater();
        this.hasStartedTrackingProgressDuringAppLaunch = true;
        this.progressInterval = window.setInterval(() => {
            this.client?.progressEvent(this.currentTime);
        }, PROGRESS_INTERVAL_SECONDS * 1000);
    };

    onAppErrored = () => {
        this.client?.progressEvent(this.currentTime);
        this.clearProgressInterval();
        this.clearSessionUpdater();
    };

    clearProgressInterval = () => {
        clearInterval(this.progressInterval);
    };
}

export default new OztamBridge();
