import { Messengers } from "../helpers/Messengers";
import { PlayerStates } from "../helpers/utils";
import { Store } from "../helpers/ten-store";
import { getLiveChannelData } from "../ten-services/ten-api";
import {
    TealiumClient,
    TealiumErrorCode,
    TealiumErrorContext,
} from "./TealiumClient";

const NONE_FOUND = "none-found";
const NA = "na";
const EXPANDED = "expand";
const ACCEPT_INVITATION = "acceptInvitation";

const AD_TYPE = {
    INNOVID: "video_ad_innovid",
    PAUSE: "video_ad_pause",
    INTERACTIVE_PAUSE: "video_ad_intpause",
    PREROLL: "video_ad_preroll",
    MIDROLL: "video_ad_midroll",
};

const AD_EVT_TYPE = {
    START: "ad_start",
    END: "ad_end",
    SELECT: "adSelect",
};

const currentEpochTimestamp = () => Math.floor(Date.now() / 1000);
const isInnovidAd = (daiAd) =>
    daiAd?.adSystem?.toLowerCase().includes("innovid");
const toLowerWithoutSpaces = (str) =>
    typeof str !== "string"
        ? str
        : str.toLowerCase().trim().split(" ").join("");

class TealiumBridge {
    tealium = new TealiumClient();
    playerValuesCache = {};
    playerState = PlayerStates.Initialising;

    currentAd = null;
    hasPlayed = false;
    liveUpdateInterval = null;

    currentTime = 0;
    oztamSessionId = "";
    lastAdStartTime = 0;

    listeners = [];

    init = () => {
        this.removeEventListeners(); // in case monitoringTeardown isn't called
        this.tealium.init();
        this.addListeners();
    };

    refreshApiBase = () => {
        const apiBase = Store.AppConfig?.account?.tealium?.restApiEndpoint;
        if (apiBase) {
            this.tealium.setApiBase(apiBase);
        }
    };

    addListeners = () => {
        this.listeners = [
            ...this.listeners,
            Messengers.oztamSessionId$.addReceiver(
                this.onOztamSessionIdChange,
                true
            ),
            Messengers.playerState$.addReceiver(this.onPlayerStateChange, true),
            Messengers.streamReady$.addReceiver(
                this.onChannelDataReceived,
                true
            ),
            Messengers.currentAd$.addReceiver(this.onAdStateChange),
            Messengers.timeUpdate$.addReceiver(this.onTimeUpdate),
            Messengers.tealiumTrackError$.addReceiver(this.onAppErrorReported),
            Messengers.playerError$.addReceiver(this.onPlayerError),
            Messengers.appTeardown$.addReceiver(this.onAppTeardown),
        ];
    };

    removeEventListeners = () => {
        this.listeners.forEach((receiver) => receiver.unlisten());
        this.listeners = [];
    };

    onAppTeardown = () => {
        this.removeEventListeners();

        this.tealium.onVideoEvent("video_exit", this.playerValuesCache);
        this.tealium.onExit();
        this.playerValuesCache = {};

        this.currentAd = null;
        this.hasPlayed = false;
        if (this.liveUpdateInterval) {
            clearInterval(this.liveUpdateInterval);
        }
        this.liveUpdateInterval = null;

        this.currentTime = 0;
        this.oztamSessionId = "";
        this.lastAdStartTime = 0;
        this.playerState = PlayerStates.Initialising;
    };

    onOztamSessionIdChange = (oztamSessionId) => {
        this.oztamSessionId = oztamSessionId;

        this.playerValuesCache = {
            ...this.playerValuesCache,
            ...(this.oztamSessionId && { oztamSessionId: this.oztamSessionId }),
        };
    };

    sendVideoLoadEvent = () => {
        this.tealium.onVideoEvent("video_load", this.playerValuesCache);
    };

    onChannelDataReceived = (channelData) => {
        if (!channelData) return;

        this.setLivePlayerValues(channelData);
        this.liveUpdateInterval = window.setInterval(async () => {
            try {
                const liveChannelData = await getLiveChannelData(
                    this.playerValuesCache.showChannel
                );
                if (liveChannelData) {
                    let hasCurrentLiveShowChanged;
                    if (liveChannelData?.liveShow.id) {
                        const { id: showId, title } = liveChannelData.liveShow;
                        hasCurrentLiveShowChanged =
                            (this.playerValuesCache.showId &&
                                showId !== this.playerValuesCache.showId) ||
                            (this.playerValuesCache.showName &&
                                title !== this.playerValuesCache.showName);
                    }

                    this.setLivePlayerValues(liveChannelData);

                    if (hasCurrentLiveShowChanged) {
                        await this.tealium.onVideoEvent(
                            "video_start",
                            this.playerValuesCache
                        );
                        await this.tealium.onVideoEvent(
                            "video_segment_view",
                            this.playerValuesCache
                        );
                    }
                }
            } catch (err) {}
        }, 60 * 1000);

        this.sendVideoLoadEvent();
    };

    setLivePlayerValues = (liveObject) => {
        const region = Store.region.state;
        const liveVideo = liveObject;

        const { endTime, startTime, id } = liveVideo?.liveShow || {};

        const showId = id || NONE_FOUND;

        this.playerValuesCache = {
            showId,
            showAlias: toLowerWithoutSpaces(liveVideo.liveShow.title),
            showChannel: liveVideo.channel.id,
            showContentFormatType: "Live",
            showContentId: showId,
            showContentVideoId: showId,
            showGenre: liveVideo.liveShow.genre || NONE_FOUND,
            showName: liveVideo.liveShow.title,
            showProgramClassification: liveVideo.liveShow.ratings,
            showContentName: liveVideo.liveShow.title,
            isMemberGatedLive: String(!!liveVideo.memberGated),
            showContentMarket: region,
            oztamPublisherId: liveVideo.liveShow.publisherId,
            videoDuration: Math.abs(endTime - startTime) || NONE_FOUND,
            ...(this.oztamSessionId && { oztamSessionId: this.oztamSessionId }),
        };
    };

    onPlayerStateChange = (playerState) => {
        if (!this.hasPlayed && playerState === PlayerStates.Playing) {
            this.hasPlayed = true;
            this.tealium.onVideoEvent("video_start", {
                ...this.playerValuesCache,
                upNextPlay: false,
            });
            if (!this.currentAd) {
                this.tealium.onVideoEvent(
                    "video_segment_view",
                    this.playerValuesCache
                );
            }
        }
    };

    getCreativeAdId = (ad) => {
        const creativeId = ad?.creativeId || ad?.creativeAdId || ad?.adId;
        if (creativeId) {
            return creativeId;
        }

        const wrapper = ad?.wrappers?.find(
            (wrapper) => wrapper?.creative_id || wrapper?.ad_id
        );

        return wrapper?.creative_id || wrapper?.ad_id || NONE_FOUND;
    };

    getAdEventValues = (ad, adEventValues, customAdType) => {
        const isInnovidAdEvent = ad && isInnovidAd(ad);
        const baseAdTypeStr = isInnovidAdEvent
            ? AD_TYPE.INNOVID
            : adEventValues.adType;

        return {
            ...adEventValues,
            adType: (customAdType && AD_TYPE?.[customAdType]) || baseAdTypeStr,
            adAssetId: this.getCreativeAdId(ad),
            ...(isInnovidAdEvent && {
                adBrandName: NA,
                adCampaign: NA,
                adIndustry: NA,
            }),
        };
    };

    onAdStateChange = (currentAd) => {
        const previousAd = this.currentAd;
        const adEventValues = {
            adType: AD_TYPE.MIDROLL,
            ...this.playerValuesCache,
        };

        const previousAdEventValues =
            previousAd &&
            this.getAdEventValues(previousAd, {
                ...adEventValues,
                adFinishTime: currentEpochTimestamp(),
            });

        const currentAdEventValues =
            currentAd &&
            this.getAdEventValues(currentAd, {
                ...adEventValues,
                adStartTime: currentEpochTimestamp(),
            });

        if (previousAd && currentAd) {
            if (this.lastAdStartTime) {
                previousAdEventValues.adWatchedDurationSec =
                    currentEpochTimestamp() - this.lastAdStartTime;
                this.lastAdStartTime = 0;
            }
            this.tealium.onVideoEvent(AD_EVT_TYPE.END, previousAdEventValues);
            this.tealium.onVideoEvent(AD_EVT_TYPE.START, currentAdEventValues);
            this.lastAdStartTime = currentAdEventValues.adStartTime;
        } else if (previousAd && !currentAd) {
            if (this.lastAdStartTime) {
                previousAdEventValues.adWatchedDurationSec =
                    currentEpochTimestamp() - this.lastAdStartTime;
                this.lastAdStartTime = 0;
            }
            this.tealium.onVideoEvent(AD_EVT_TYPE.END, previousAdEventValues);
            this.tealium.onVideoEvent(
                "video_segment_view",
                this.playerValuesCache
            );
        } else if (!previousAd && currentAd) {
            this.tealium.onVideoEvent(AD_EVT_TYPE.START, currentAdEventValues);
            this.lastAdStartTime = currentAdEventValues.adStartTime;
        }
        this.currentAd = currentAd;
    };

    onTimeUpdate = (time) => {
        this.currentTime = time;
    };

    onAppErrorReported = (tealiumErrorData) => {
        if (!tealiumErrorData) return;

        const {
            errorCode,
            errorContext,
            errorSource,
            errorDetail,
            errorMessage,
        } = tealiumErrorData;
        const modError = {
            errorCode: `${errorContext}:${errorCode}`,
            errorType: errorContext,
            errorDetail,
            errorSource: errorSource || window.location.pathname,
            ...(errorMessage ? { errorMessage } : {}),
        };
        this.tealium.onErrorEvent(modError);
    };

    onPlayerError = (errorData) => {
        // always seems to be fatal with this AAMP event
        const errorMessage = `Parent error ${errorData?.code}: ${errorData?.message}`;
        Messengers.tealiumTrackError$.send({
            errorCode: TealiumErrorCode.Hls,
            errorContext: TealiumErrorContext.STREAM,
            errorDetail: errorMessage,
            errorSource: "",
            errorMessage: errorMessage,
        });
    };
}
export default new TealiumBridge();
