import CryptoJS from "../lib/crypto-js";
import { Store } from "../helpers/ten-store";
import { ErrorTypes, MLEError } from "../../provider-utils";
import { buildGoogleAdUrl, buildFreewheelAdUrl } from "./ten-ad-parameters.js";
import { Messengers } from "../helpers/Messengers.js";
import {sendVozStreamingRequest} from "./ten-voz-request.js"
import {
    TealiumErrorCode,
    TealiumErrorContext,
} from "../analytics/TealiumClient.js";

const HmacSHA256 = CryptoJS.HmacSHA256;
const Hex = CryptoJS.enc.Hex;
let playbackResponse;
let playbackAuthToken;

export const sendTenApiRequest = async (
    url,
    method = "GET",
    header = {},
    requestBody = null,
    returnResponseDataAndHeaders = false,
) => {
    const key = Hex.parse(Store.ApiConfig.apiKey);
    const timestamp = Date.now().toString();
    const signature = Hex.stringify(HmacSHA256(`${timestamp}:${url}`, key));
    const signatureText = `${timestamp}_${signature}`;

    const token = Store.auth.accessToken;
    let tokenHeader = {};
    if (token) {
        tokenHeader = {
            Authorization: `Bearer ${token}`,
        };
    }

    const headers = {
        "X-N10-SIG": signatureText,
        "Content-Type": "application/json",
    };

    const options = {
        method,
        headers: { ...headers, ...tokenHeader, ...header },
    };

    if (requestBody) {
        options.body = JSON.stringify(requestBody);
    }

    const tenRequest = await fetch(url, options);
    if (!tenRequest.ok) throw tenRequest;

    const responseData = await tenRequest.json();
    if (returnResponseDataAndHeaders) {
        return { responseData, headers: tenRequest.headers };
    } else {
        return responseData;
    }
};

export const getLiveChannelData = async (channelId) => {
    const liveTvEndpoint = Store.AppConfig.liveTvEndpoint;
    let apiResponse;
    try {
        apiResponse = await sendTenApiRequest(liveTvEndpoint);
    } catch (err) {
        console.warn("[acc]", err);
        throw new MLEError(
            "Could not retrieve live channel from 10 API",
            ErrorTypes.PROVIDER,
            err.status
        );
    }

    return apiResponse.find((liveObj) => liveObj.channel.id === channelId);
};

export const getPlaybackInfo = async (channelId) => {
    const matchingChannel = await getLiveChannelData(channelId);
    if (!matchingChannel) {
        throw new MLEError(
            "Could not find matching live channel",
            ErrorTypes.CHANNEL_MAP
        );
    }

    const liveShow = matchingChannel.liveShow;

    const playbackEndpoint = liveShow.playbackApiEndpoint;

    const isFreeWheelEnabled = Store.isFreewheelEnabled();

    try {
        if (isFreeWheelEnabled) {
        const header = { 'TP-AcceptFeature': 'v1/fw;', 'TP-Platform': 'UAP', }
        const [, playbackData] = await Promise.all([
            sendVozStreamingRequest(matchingChannel),
            sendTenApiRequest(
                playbackEndpoint,
                'GET',
                header,
                null,
                true
              )
        ])

        playbackAuthToken = playbackData.headers.get('x-dai-auth');
        playbackResponse = playbackData.responseData;
        } else {
            playbackResponse = await sendTenApiRequest(playbackEndpoint);

        }
    } catch (error) {
        Messengers.tealiumTrackError$.send({
            errorCode: TealiumErrorCode.FetchVideoPlayback,
            errorContext: TealiumErrorContext.API,
            errorDetail: JSON.stringify(error),
            errorSource: playbackEndpoint,
            errorMessage: error.message,
        });
        throw new MLEError(
            "Error retrieving live channel playback info",
            ErrorTypes.PROVIDER,
            error.status
        );
    }

    let liveStreamId = "<empty>";
    if (matchingChannel.channel.id === "TenShake" && liveShow.publisherId) {
        liveStreamId = liveShow.publisherId;
    } else if (playbackEndpoint) {
        const idOnEndpoint = playbackEndpoint.substring(
            playbackEndpoint.lastIndexOf("/") + 1
        );
        if (idOnEndpoint) {
            liveStreamId = idOnEndpoint;
        }
    }

    // set here so the ten shake logic is done once, can get in oztam etc
    matchingChannel.liveStreamId = liveStreamId;
    Messengers.streamReady$.send(matchingChannel);

    return {
        daiAssetKey: playbackResponse.dai.assetKey,
        liveShow,
        liveStreamId,
    };
};

export const getSourceUrlFromGoogle = async ({
    daiAssetKey,
    liveStreamId,
    liveShow,
}) => {

    let postBody;
    const isFreeWheelEnabled = Store.isFreewheelEnabled();
    const {fwLiveAdTag} = Store.AppConfig.adsConfig || '';
    if (isFreeWheelEnabled) {
        postBody = await buildFreewheelAdUrl(
        fwLiveAdTag,
        liveShow.genre,
        Messengers.oztamSessionId$.getCurrentValue(),
        playbackAuthToken,
        liveStreamId);


    } else {
        const adTagParameter = Store.AppConfig.adsConfig.daiAdTagParameter;
        postBody = buildGoogleAdUrl(
            adTagParameter,
            liveStreamId,
            liveShow.genre,
            Messengers.oztamSessionId$.getCurrentValue()
        );
    }

    try {
        const daiUrl = `https://dai.google.com/linear/v1/hls/event/${daiAssetKey}/stream`;
        const daiRequest = await fetch(daiUrl, {
            method: "POST",
            headers: {
                "content-type": "application/x-www-form-urlencoded",
            },
            body: postBody,
        });
        const daiResponse = await daiRequest.json();

        Messengers.googleDaiResponse$.send(daiResponse);
        return daiResponse.stream_manifest;
    } catch (err) {
        Messengers.tealiumTrackError$.send({
            errorCode: TealiumErrorCode.DaiLive,
            errorContext: TealiumErrorContext.STREAM,
            errorDetail: JSON.stringify(err),
            errorSource: `assetKey: ${daiAssetKey}`,
            // eslint-disable-next-line @typescript-eslint/no-explicit-any
            errorMessage: err.message,
        });
        throw new MLEError(
            "Error retrieving Google DAI stream",
            ErrorTypes.PROVIDER,
            err.status
        );
    }
};

export const tryGetAndSetIpaddress = async (foxtelRegionCode) => {

    const ipAddressEndpoint = `${Store.ApiConfig.apiBaseUrl}/whatismyip`;
        try {
            const ipAddressResponse = await sendTenApiRequest(
                ipAddressEndpoint
            );
            Store.ipAddress = ipAddressResponse;
        } catch (e) {
            console.log(
                `[acc] Oztam failed to get IP, continuing with ${Store.ipAddress.ip}`
            );
        }
};

export const tryGetAndSetUserRegion = async (foxtelRegionCode) => {
    if (foxtelRegionCode) {
        Store.region = { allow: true, state: foxtelRegionCode };
        return;
    }

    try {
        const regionData = await sendTenApiRequest(
            Store.ApiConfig.regionServiceUrl
        );
        Store.region = regionData;
    } catch (error) {
        console.warn(
            "[acc] could not get region, using NSW as fallback",
            error.toString()
        );
    }
};

export const getAndSetAppConfig = async () => {
    const queryParams = `SystemName=${Store.ApiConfig.systemName}&manufacturer=${Store.ApiConfig.manufacturer}`;
    const configUrl = `${Store.ApiConfig.apiBaseUrl}/api/${Store.ApiConfig.apiVersionTag}/config?${queryParams}`;
    try {
        const appConfig = await sendTenApiRequest(configUrl);
        Store.AppConfig = appConfig;
    } catch (err) {
        Messengers.tealiumTrackError$.send({
            errorCode: TealiumErrorCode.FetchAppConfig,
            errorContext: TealiumErrorContext.API,
            errorDetail: JSON.stringify(err),
            errorSource: configUrl,
            errorMessage: "",
        });
        throw new MLEError(
            "Could not initialize with Ten App Config",
            ErrorTypes.PROVIDER,
            err.status
        );
    }
};

