import {
    TealiumErrorCode,
    TealiumErrorContext,
} from "../analytics/TealiumClient";
import { Messengers } from "../helpers/Messengers";
import { Store } from "../helpers/ten-store";
import { fetchFMSResponse } from "./ten-fms-request";

import {
    ErrorTypes,
    MLEError,
    setTokenInDeviceStorage,
} from "../../provider-utils";
import { sendTenApiRequest } from "./ten-api";

let refreshTimeout;

export const refreshAuth = async (
    refreshToken,
    alternativeToken,
    retryCount = 0
) => {
    let postBody = {
        refreshToken: refreshToken,
        alternativeToken: alternativeToken,
    };
    const maxRetries = 2;
    const refreshTokenUrl = Store.AppConfig.authConfig.refreshToken;

    try {
        if (refreshTokenUrl) {
            const refreshTokenResponse = await sendTenApiRequest(
                refreshTokenUrl,
                "POST",
                {},
                postBody
            );

            if (
                refreshTokenResponse.refreshToken &&
                refreshTokenResponse.accessToken &&
                refreshTokenResponse.alternativeToken
            ) {
                Store.auth = refreshTokenResponse;

                // If another part of initialization errors between here and the FMS request,
                // our new refresh token will not be stored, and the user will be logged out.

                // If the cached FMS data has not yet expired, we do not need to make another request to update Sky storage.
                // So, we end up updating Sky storage twice only in the case that FMS data needed to be updated, and the app initialized successfully.
                await setTokenInDeviceStorage(
                    "au.com.ten",
                    btoa(
                        JSON.stringify({
                            refreshToken: Store.auth.refreshToken,
                            alternativeToken: Store.auth.alternativeToken,
                            fmsResponse: Store.fmsResponse,
                            fmsResponseTimestamp: Store.fmsResponseTimestamp,
                        })
                    )
                );

                retryCount = 0;

                // Set the refresh timeout for the next token refresh
                refreshTimeout = window.setTimeout(() => {
                    refreshAuth(
                        Store.auth.refreshToken,
                        Store.auth.alternativeToken
                    );
                }, refreshTokenResponse.expiresIn * 1000);
            } else {
                throw new MLEError(
                    "Invalid refresh response.",
                    ErrorTypes.AUTH,
                    refreshTokenResponse.status
                );
            }
        }
    } catch (err) {
        if (retryCount < maxRetries) {
            return await refreshAuth(
                refreshToken,
                alternativeToken,
                retryCount + 1
            );
        } else {
            console.log("[acc] Authentication refresh failed!");
            Messengers.tealiumTrackError$.send({
                errorCode: TealiumErrorCode.RefreshToken,
                errorContext: TealiumErrorContext.API,
                errorDetail: JSON.stringify(err),
                errorSource: refreshToken,
                errorMessage: "",
            });
            throw err;
        }
    }
};

export const tryGetAndSetUserInfo = async () => {
    const userEndpoint = Store.AppConfig.userEndpoints.fetchUserApiEndpoint;
    try {
        const userInfo = await sendTenApiRequest(userEndpoint);
        Store.UserInfo = userInfo;
    } catch (err) {
        console.warn(
            "[acc] could not get user info, ad tracking will be affected",
            err.toString()
        );
        Messengers.tealiumTrackError$.send({
            errorCode: TealiumErrorCode.FetchUser,
            errorContext: TealiumErrorContext.API,
            errorDetail: JSON.stringify(err),
            errorSource: userEndpoint,
            errorMessage: err.message,
        });
    }
};

export const authApiBundle = async (refreshToken, alternativeToken) => {
    try {
        await refreshAuth(refreshToken, alternativeToken);
    } catch (err) {
        throw new MLEError(
            "Could not perform TEN auth refresh",
            ErrorTypes.AUTH,
            err.status
        );
    }
    await tryGetAndSetUserInfo();
    await fetchFMSResponse()
};

Messengers.appTeardown$.addReceiver(() => {
    if (refreshTimeout) clearTimeout(refreshTimeout);
});
