import { Store } from "./helpers/ten-store";
import { MessengerLink } from "./helpers/MessengerLink";
import { Messengers } from "./helpers/Messengers";
import { ErrorTypes, MLEError } from "../provider-utils";
import { NetworkConnectionType } from "./helpers/utils";
import {
    getAndSetAppConfig,
    tryGetAndSetUserRegion,
    tryGetAndSetIpaddress,
    getPlaybackInfo,
    getSourceUrlFromGoogle,
} from "./ten-services/ten-api";
import { authApiBundle } from "./ten-services/ten-auth";

import LiveGoogleDai from "./ten-services/live-google-dai";
import TealiumBridge from "./analytics/TealiumBridge";
import OztamBridge from "./analytics/OztamBridge";

export default class TenProvider {
    config;
    messengerLink;
    videoElement;

    constructor(config) {
        this.config = config;
    }

    /**
     * @typedef {import('../../app/get-asset').GetAssetRequest} GetAssetRequest
     * @typedef {import('../../app/get-asset').GetAssetResponse} GetAssetResponse
     */
    /**
     * @param {GetAssetRequest} args requested asset and device information
     * @returns {GetAssetResponse} streams for the requested asset
     */
    async getAsset({
        authToken,
        glassAssetLocator,
        glassContentOptions,
        deviceInfo,
    }) {
        const start = performance.now();
        let gracenoteId = getFileName(glassAssetLocator.pathname);
        const provider = glassAssetLocator.searchParams.get("provider");
        let providerId = glassAssetLocator.searchParams.get("providerid");

        const {
            glassConfigure: {
                brand: deviceBrand,
                version: deviceOSVersion,
                model: deviceModel,
            },
            uuid: deviceId,
        } = deviceInfo;
        const { connectionType: networkConnectionType } = glassContentOptions;

        let channelId;
        let stateCode;
        try {
            const channelInfo = JSON.parse(providerId);
            if (!channelInfo.id) {
                throw new Error();
            }
            channelId = channelInfo.id;

            if (
                ["nsw", "qld", "wa", "nt", "sa", "act", "vic", "tas"].includes(
                    channelInfo.region?.toLowerCase(),
                )
            ) {
                stateCode = channelInfo.region.toUpperCase();
            }
        } catch (err) {
            throw new MLEError(
                "Could not parse 10Play channelID",
                ErrorTypes.CHANNEL_MAP,
            );
        }

        let refreshToken, alternativeToken, fmsResponse, fmsResponseTimestamp;
        try {
            const tenAuthData = JSON.parse(atob(authToken));
            const {
                refreshToken: inputRefreshToken,
                alternativeToken: inputAltToken,
                fmsResponse: fmsRes,
                fmsResponseTimestamp: fmsResTimestamp,
            } = tenAuthData;
            if (!inputAltToken || !inputRefreshToken) throw new Error();

            refreshToken = inputRefreshToken;
            alternativeToken = inputAltToken;
            fmsResponse = fmsRes;

            fmsResponseTimestamp = fmsResTimestamp;
        } catch (e) {
            throw new MLEError(
                "Could not parse TEN auth data",
                ErrorTypes.GENERIC,
            );
        }

        console.info("[acc] TEN received startup data", {
            authData: { refreshToken, alternativeToken },
            glassAssetLocator,
            glassAssetLocatorParsed: {
                provider,
                providerId,
            },
            glassContentOptions,
            deviceInfo,
        });

        console.info(
            `[acc] Accedo version ${Store.appVersion}, parent app version ${deviceInfo.appVersion}, player version ${deviceInfo.playerVersion}`,
        );

        Store.deviceId = deviceId;
        Store.deviceBrand = deviceBrand;
        Store.devicePlatform = "SoIP"; // hardcode until firebolt is sunsetted
        Store.deviceModel = "Xi1"; // hardcode until firebolt is sunsetted
        Store.deviceFirmwareVersion = deviceOSVersion;
        Store.appIsRunning = true;
        Store.fmsResponse = fmsResponse;
        Store.fmsResponseTimestamp = fmsResponseTimestamp;
        if (networkConnectionType.toLowerCase() === "ethernet") {
            Store.deviceNetworkConnection = NetworkConnectionType.LAN;
        } else if (networkConnectionType.toLowerCase() === "wifi") {
            Store.deviceNetworkConnection = NetworkConnectionType.WIFI;
        }

        // init tealium here so API startup errors can be tracked
        TealiumBridge.init();

        // need app config for user endpoint
        // Expect errors to be thrown from subpromises
        await Promise.all([
            tryGetAndSetUserRegion(stateCode),
            getAndSetAppConfig(),
            tryGetAndSetIpaddress(),
        ]);

        // App config request has given us a Tealium API base url
        TealiumBridge.refreshApiBase();

        await authApiBundle(refreshToken, alternativeToken);
        const liveData = await getPlaybackInfo(channelId);

        // need user info, app config and playback info to get source url
        const sourceUrl = await getSourceUrlFromGoogle(liveData);

        console.log(
            `[acc] passing back url ${sourceUrl} after ${
                (performance.now() - start) / 1000
            }s`,
        );
        return {
            aampInitConfigOverrides: {
                ...this.config?.aampInitConfigRemoteOverrides,
            },
            streams: [{ url: sourceUrl }],
        };
    }

    monitoringSetup(videoElement) {
        console.log("[acc] call monitoringSetup");
        if (!this.messengerLink) {
            this.messengerLink = new MessengerLink(Messengers);
        }
        this.videoElement = videoElement;
        this.messengerLink.unlinkVideoElementListeners(this.videoElement);
        this.messengerLink.linkVideoElementListeners(this.videoElement);

        // expect modules to destroy themselves when appTeardown$ fires
        LiveGoogleDai.init();
        OztamBridge.init();
    }

    monitoringTeardown() {
        console.log("[acc] call monitoringTeardown");
        if (this.messengerLink && this.videoElement) {
            this.messengerLink.unlinkVideoElementListeners(this.videoElement);
        }
        Messengers.appTeardown$.send();
        Object.keys(Messengers).forEach((key) => Messengers[key].reset());
        Store.appIsRunning = false;
    }
}

function getFileName(url) {
    if (url) {
        return url.substring(url.lastIndexOf("/") + 1, url.lastIndexOf("."));
    }

    return "";
}
